@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300&display=swap);
:root{
    --primary-color: #047aed;
    --secondary-color: #1c3fa8;
    --dark-color: #002240;
    --light-color: #f4f4f4;
    --success-color: #5cb85c;
    --error-color:#d9534f;
}

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    font-family: 'Lato',sans-serif;
    color: #333;
    line-height: 1.6;
    scrollbar-width: 0;
}
ul{
    list-style-type: none;
}
a{
    text-decoration: none;
    color: #333;
}
h1,h2{
    font-weight: 300;
    line-height: 1.2;
    margin: 10px 0;
}
p{
    margin: 10px 0;
}
img{
    width: 100%;
}

code,pre {
    background-color: #333;
    padding: 10px;
    color: #fff;
}

/* Navbar */

.navbar{
    background-color: #047aed;
    background-color: var(--primary-color);
    color: #fff;
    height: 70px;
}
.navbar ul{
    display: flex;
}
.navbar a{
    color: #fff;
    padding: 10px;
    margin: 0 5px;
}
.navbar a:hover{
    border-bottom: 2px solid #fff;
    
}
.navbar .flex{
    justify-content: space-between;
}

/* Showcase */
.showcase{
    height: 400px;
    background-color: #047aed;
    background-color: var(--primary-color);
    color: #fff;
    position: relative;
}
.showcase h1{
    font-size: 40px;
}
.showcase p{
    margin: 20px 0;
}
.showcase .grid{
    grid-template-columns: 55% 45%;
    grid-gap: 30px;
    gap: 30px;
    overflow: visible;
}

.showcase-text {
    -webkit-animation: slideInFromLeft 1s ease-in;
            animation: slideInFromLeft 1s ease-in;
}

.showcase-form{
    position: relative;
    top: 60px;
    height: 350px;
    width: 400px;
    padding: 40px;
    z-index: 100;
    justify-self: flex-end;
    -webkit-animation: slideInFromRight 1s ease-in;
            animation: slideInFromRight 1s ease-in;
}
.showcase-form .form-control{
    margin: 30px 0;
}
.showcase-form input[type="text"],
.showcase-form input[type="email"]
{
    border: 0;
    border-bottom: 1px solid #b4becb;
    width: 100%;
    font-size: 16px;
    padding: 3px;
}

.showcase-form input:focus{
    outline: none;
}

.showcase::before,
.showcase::after{
    content: '';
    background-color: #fff;
    height: 100px;
    position: absolute;
    bottom:-70px;
    left: 0;
    right: 0;
    transform: skewY(-3deg);
    -webkit-transform: skewY(-3deg);
    -moz-transform: skewY(-3deg);
    -ms-transform: skewY(-3deg);
}

/* stats */
.stats{
    padding-top: 100px;
    -webkit-animation: slideInFromBottom 1s ease-in;
            animation: slideInFromBottom 1s ease-in;
}

.stats-heading{
    max-width: 500px;
    margin: auto;
}

.stats .grid h3{
    font-size: 35px;
}
.stats .grid p{
    font-weight: bold;
    font-size: 20px;
}

/* Cli */

.cli .grid{
    grid-template-columns: repeat(3 , 1fr);
    grid-template-rows: repeat(2 , 1fr);
}

.cli .grid > *:first-child{
    grid-column: 1 / span 2;
    grid-row: 1 / span 2;
}

/* Cloud */

.cloud .grid{
    grid-template-columns: 4fr 3fr;
}

/* Languages */

.languages .flex{
    flex-wrap: wrap;
}

.languages .card{
    text-align: center;
    margin: 18px 10px 40px ;
    transition: -webkit-transform .2s ease-in;
    transition: transform .2s ease-in;
    transition: transform .2s ease-in, -webkit-transform .2s ease-in;
}

.languages .card h4{
    font-size: 20px;
    margin-bottom: 10px;
}

.languages .card:hover{
    -webkit-transform: translateY(-15px);
            transform: translateY(-15px);
}


/* Features */
.features-head .grid {
    overflow:visible;
}

.features-head .grid > *:first-child,
.docs-head .grid > *:first-child {
    -webkit-animation: slideInFromLeft 1s ease-in;
            animation: slideInFromLeft 1s ease-in;
}

.features-head img,
.docs-head img{
    width: 200px;
    justify-self: flex-end;
    -webkit-animation: slideInFromRight 1s ease-in;
            animation: slideInFromRight 1s ease-in;
}

.features-sub-head img {
    width: 300px;
    justify-self: flex-end;
}

.features-main .grid {
    padding-bottom: 30px;
}

.features-main .grid > *:first-child {
    grid-column: 1 / span 3;
}
.features-main .grid > *:nth-child(2) {
    grid-column: 1 / span 2;
}

/* Docs */

.docs-head .grid {
    overflow:visible;
}

.docs-main h3 {
    margin: 20px 0;
}

.docs-main .grid {
    grid-template-columns: 1fr 2fr;
    align-items: flex-start;
}

.docs-main nav li {
    font-size: 17px;
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid #ccc;
}

.docs-main a :hover {
    font-weight: bold;
}
/* Footer */
.footer{
    padding-bottom: 15px;
}
.footer nav ul li{
    margin: 0 10px;
}

.footer .social a div{
    width: 20px;
    height: 20px;
    margin: 0 10px;
}

/* Animations */

@-webkit-keyframes slideInFromLeft {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slideInFromLeft {
    0% {
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes slideInFromRight {
    0% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@keyframes slideInFromRight {
    0% {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }

    100% {
        -webkit-transform: translateX(0);
                transform: translateX(0);
    }
}

@-webkit-keyframes slideInFromTop {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideInFromTop {
    0% {
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@-webkit-keyframes slideInFromBottom {
    0% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}

@keyframes slideInFromBottom {
    0% {
        -webkit-transform: translateY(100%);
                transform: translateY(100%);
    }

    100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
    }
}


/* Tablets and under */
@media(max-width: 768px) {
    .grid,
    .showcase .grid,
    .stats .grid,
    .cli .grid,
    .cloud .grid,
    .features-main .grid,
    .docs-main .grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr;
    }

    .showcase {
        height: auto;
    }

    .showcase-text{
        text-align: center;
        margin-top: 40px;
        -webkit-animation: slideInFromTop 1s ease-in;
                animation: slideInFromTop 1s ease-in;
    }

    .showcase-form {
        justify-self: center;
        margin:auto ;
        -webkit-animation: slideInFromBottom 1s ease-in;
                animation: slideInFromBottom 1s ease-in;
    }

    .cli .grid > *:first-child{
        grid-column: 1;
        grid-row: 1;
    }
    .footer .container div {
        text-align: center;
    }
    .features-head,
    .features-sub-head,
    .docs-head {
        text-align: center;
    }

    .features-head img,
    .features-sub-head img,
    .docs-head img {
        justify-self: center;
    }

    .features-main .grid > *:first-child,
    .features-main .grid > *:nth-child(2){
        grid-column: 1;
    }

    /* animations */

    .docs-head .grid > *:first-child,
    .features-head .grid > *:first-child { 
        -webkit-animation: slideInFromTop 1s ease-in; 
                animation: slideInFromTop 1s ease-in;
    }
    .docs-head .grid > *:nth-child(2),
    .features-head .grid > *:nth-child(2) { 
        -webkit-animation: slideInFromBottom 1s ease-in; 
                animation: slideInFromBottom 1s ease-in;
    }
    

}

/* Mobile */
@media(max-width: 500px) {
    .navbar{
        height: 110px;
    }
    
    .navbar .flex {
        flex-direction: column;
    }

    .navbar ul {
        padding: 10px ;
        background-color: rgba(0, 0, 0, .1);
    }
}
.container{
    max-width: 1100px;
    margin: 0 auto;
    overflow: auto;
    padding: 0 40px;
}

.card{
    background-color: #fff;
    color: #333;
    border-radius: 10px;
    box-shadow: 0 3px 10px rgba(0, 0, 0, .2);
    padding: 20px;
    margin: 10px;
}

.btn{
    background-color: var(--primary-color);
    color: #fff;
    padding: 10px 30px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
}

.btn-outline{
    background-color: transparent;
    border:1px solid #fff;
}

.btn:hover{
    -webkit-transform: scale(.98);
            transform: scale(.98);
}

/* Backgrounds & colored buttons*/

.bg-primary,
.btn-primary{
    background-color: var(--primary-color);
    color: #fff;
}

.bg-secondary,
.btn-secondary{
    background-color: var(--secondary-color);
    color: #fff;
}

.bg-dark,
.btn-dark{
    background-color: var(--dark-color);
    color: #fff;
}

.bg-light,
.btn-light{
    background-color: var(--light-color);
    color: #333;
}
.bg-primary a,
.btn-primary a,
.bg-secondary a,
.btn-secondary a,
.bg-dark a,
.btn-dark a{
    color: #fff;
}

/* Text colors */

.text-primary{
    color: var(--primary-color);
}

.text-secondary{
    color: var(--secondary-color);
}

.text-dark{
    color: var(--dark-color);
}

.text-light{
    color: var(--light-color);
}

/* Text Sizes */
.lead{
    font-size: 20px;
}

.sm{
    font-size: 1rem;
}

.md{
    font-size: 2rem;
}

.lg{
    font-size: 3rem;
}

.xl{
    font-size: 4rem;
}

.text-center{
    text-align: center;
}

/* Alert */
.alert {
    background-color: var(--light-color);
    padding-bottom: 10px 20px;
    margin:15px 0;
    font-weight: bold;
    padding:10px;
}

.alert-success {
    background-color: var(--success-color);
    color: #fff;
}

.alert-error {
    background-color: var(--error-color);
    color: #fff;
}

